import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { TableData } from 'components/common';
import { Card, Row, Col } from 'antd';
import { BtnAddNew } from 'components/common/button';
import { getPaperStudent } from 'services/paperStudent';
import { getQueryParams } from 'utils/urlUtil';
import SearchBar from './SearchBar';

class ReturnDocPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  columns = [
    {
      title: 'Mã sv',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => <BtnAddNew href={`#/return-doc/form/${record.studentId}`} />,
    },
  ];

  render() {
    const { trainTypeId, facultyId, courseId } = this.state.paramSearch;
    return (
      <Card title="Trả giấy tờ">
        <Row>
          <SearchBar />
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              allowGetData={!!trainTypeId && !!facultyId && !!courseId}
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getPaperStudent}
              columns={this.columns}
              rowKey="studentId"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReturnDocPage />
  </Page>
);



